import React from 'react'
import PersonIcon from '@mui/icons-material/Person';
import MarkUnreadChatAltIcon from '@mui/icons-material/MarkUnreadChatAlt';
import { IconButton } from '@mui/material';
import './Header.css'

function Header() {
  return (
    <div className='tinder-header'>
        <IconButton>
      <PersonIcon className='header-icons' fontSize='large' />
      </IconButton>
      <IconButton>
      <img src='https://www.logo.wine/a/logo/Tinder_(app)/Tinder_(app)-Flame-Logo.wine.svg' />
      </IconButton>
      <IconButton>
      <MarkUnreadChatAltIcon className='header-icons' fontSize='large' />
      </IconButton>
    </div>
  )
}

export default Header
